@keyframes ripple {
  0% {
    box-shadow: 0 0 0 1px transparent;
  }
  50% {
    box-shadow: 0 0 0 15px rgba(0, 0, 0, 0.1);
  }
  100% {
    box-shadow: 0 0 0 15px transparent;
  }
}

.mdCheckbox {
  margin: 16px 0;
  color: #333;
}

.mdCheckbox.md-radio-inline {
  display: inline-block;
}

.mdCheckbox label {
  display: inline-block;
  height: 20px;
  position: relative;
  padding: 0 30px;
  margin-bottom: 0;
  cursor: pointer;
  vertical-align: bottom;
}

.mdCheckbox label::before, .mdCheckbox label::after {
  position: absolute;
  content: '';
  transition: all 0.3s ease;
  transition-property: transform, border-color;
}

.mdCheckbox label::before {
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 2px solid rgba(0, 0, 0, 0.54);
}

.mdCheckbox label::after {
  top: 5px;
  left: 5px;
  width: 10px;
  height: 10px;
  transform: scale(0);
  background: #337ab7;
}

.mdCheckbox input[type='checkbox'] {
  display: none;
}

.mdCheckbox input[type='checkbox']:checked + label::before {
  border-color: #337ab7;
  animation: ripple 0.2s linear forwards;
}

.mdCheckbox input[type='checkbox']:checked + label::after {
  transform: scale(1);
}

.mdCheckboxDesign hr {
  content: '';
  display: block;
  position: relative;
  bottom: 0;
  left: 32px;
  margin: 0;
  padding: 0;
  width: 72%;
  height: 2px;
  opacity: 0.5;
  border: none;
  background: #005695;
  font-size: 1px;
  will-change: transform, visibility;
  transition: all 300ms ease-in-out;
  transform: scaleX(0);
  visibility: hidden;
  z-index: 10;
}

.mdCheckboxDesign .Title {
  border: 0;
  width: 75%;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  left: 15px;
  position: relative;
}

.mdCheckboxDesign .Title:focus {
  border: 0;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
}

.mdCheckboxDesign .Title:focus ~ hr {
  transform: scaleX(1);
  visibility: visible;
}

.mdCheckboxDesign .Value {
  color: blue;
  border: 1px solid #d1d1d1;
}

.mdCheckboxDesign::before {
  display: inline-block;
  position: relative;
  content: '';
  left: -5px;
  top: 5px;
  width: 15px;
  height: 15px;
  background: rgba(0, 0, 0, 0.06);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  border: 2px solid rgba(0, 0, 0, 0.14);
}



